import styled from "styled-components";
import { palette } from "styled-theme";
// import { transition, borderRadius } from "../../settings/style-util";
// import WithDirection from "../../settings/withDirection";
import { Link } from "react-router-dom";

const SearchbarWrapper = styled.div`
  font-family: ${({ theme }) => theme.font};
  font-weight: 400;
  display: flex;
  align-content: center;
  align-items: center;
  margin-left: -10px;
  margin-right: -10px;
  margin-top: -10px;
  margin-bottom: 25px;
  border: 0px solid #ccc;
  background: ${({ theme }) => theme.colors.primary};
  border-bottom-left-radius: 24px;
  border-bottom-right-radius: 24px;
  padding: 5px 15px 15px 15px;
  width: 100vw;
  box-sizing: border-box;
  justify-content: space-between;
  flex-wrap: nowrap;
  flex-direction: row;
  .search-form {
    width: 100%;
    position: relative;
    input {
      background: ${({ theme }) => theme.colors.primary};
      border: 1px solid ${({ theme }) => theme.colors.secondary};
      color: ${({ theme }) => theme.colors.white};
      border-radius: 24px;
      // border-bottom-left-radius: 24px;
      // border-bottom-right-radius: 24px;
      padding-left: 16px;
    }
    .searchBtn {
      position: absolute;
      border-radius: 8px;
      border-radius: 24px;
      right: 3px;
      top: 3px;
      padding: 10px 12px;
    }
  }
  .reportBtn {
    margin: 8px 3px 3px 3px;
    border-radius: 8px;
    border-radius: 24px;
    padding: 10px 12px;
  }
`;

export default SearchbarWrapper;
export const FilterResult = styled.div`
  color: ${({ theme }) => theme.colors.text};
  font-family: ${({ theme }) => theme.font};
  display: block;
  .result {
    display: flex;
    flex-direction: column;
    .list-item {
      display: flex;
      justify-content: space-between;
      overflow: hidden;
      margin-bottom: 5px;
      border: 1px solid #ccc;
      border-radius: 12px;
      padding: 10px;

      .info {
        display: flex;
        font-size: 18px;
        // background: red;
        .pro-pic {
          min-width: 60px;
          position: relative;
          max-width: 60px;
          max-height: 60px;
          img {
            border-radius: 16px;
            overf10low: hidden;
            width: 100%;
          }
          span {
            position: absolute;
            background: #fff;
            padding: 3px;
            border-radius: 15px;
            top: -10px;
            left: -10px;
          }
          .Pending {
            color: #ffaf38;
          }
          .Complete {
            color: ${({ theme }) => theme.colors.green};
          }
          .Cancelled {
            color: ${({ theme }) => theme.colors.red};
          }
          .Overdue {
            color: ${({ theme }) => theme.colors.gray};
          }
        }
        .details {
          margin-left: 8px;
          width: 100%;
          .name {
            font-size: 14px;
            margin-bottom: 4px;
            color: ${({ theme }) => theme.colors.gray};
          }
          .amount {
            font-size: 18px;
            margin-bottom: 4px;
            font-weight: 600;
            color: ${({ theme }) => theme.colors.grayDark};
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 150px;
          }
          .date {
            font-size: 12px;
            margin-bottom: 3px;
          }
          .loc {
            font-size: 10px;
          }
        }
      }

      .action {
        display: flex;
        flex-direction: row;
        align-content: center;
        align-items: center;
        padding: 4px;
        // background: yellow;
        .icon {
          font-size: 18 px;
          &.view {
            color: ${({ theme }) => theme.colors.primary};
          }
          &.edit {
            color: ${({ theme }) => theme.colors.secondary};
          }
          &.delete {
            color: ${({ theme }) => theme.colors.red};
          }
        }
      }
    }
  }
`;
export const ActionLink = styled(Link)`
  font-size: 24px;
  padding: 4px;
`;
