import SearchbarWrapper, { FilterResult, ActionLink } from "./searchbar.style";
import { FiSearch, FiFileText } from "react-icons/fi";
import {
  ButtonBuySell,
  SectionTitle,
  Container,
} from "../../theme/GlobalStyles";
const SearchBar = ({ searchQuery, setSearchQuery }) => (
  <SearchbarWrapper>
    <form action="/dashboard/transactions" method="get" className="search-form">
      {/* <label htmlFor="header-search">
      <span className="visually-hidden">Search blog posts</span>
    </label> */}
      <input
        value={searchQuery}
        onInput={(e) => setSearchQuery(e.target.value)}
        type="text"
        id="header-search"
        placeholder="Search by name"
        name="s"
      />
      <button type="submit" className="searchBtn outline">
        <FiSearch />
      </button>
    </form>
    <div>
      {/* <button type="button" className="reportBtn">
        <FiFileText />
      </button> */}
    </div>
  </SearchbarWrapper>
);

export default SearchBar;
