import styled from "styled-components";
import { palette } from "styled-theme";
// import { transition, borderRadius } from "../../settings/style-util";
// import WithDirection from "../../settings/withDirection";
import { Link } from "react-router-dom";

const TransactionsWrapper = styled.div`
  font-family: ${({ theme }) => theme.font};
  font-weight: 400;
  display: block;
  .filters-tab {
    display: flex;
    margin-bottom: 15px;
    overflow-x: scroll;
    .filter {
      display: flex;
      align-content: center;
      align-items: center;
      margin-right: 5px;
      border: 1px solid #ccc;
      border-radius: 24px;
      // padding: 10px 15px;
      padding: 5px 12px;
      font-size: 12px;
      // &:nth-child(1) {
      //   background: ${({ theme }) => theme.colors.primary};
      //   color: ${({ theme }) => theme.colors.white};
      // }
      &.active {
        background: ${({ theme }) => theme.colors.primary};
        color: ${({ theme }) => theme.colors.white};
      }
      .filter-icon {
        padding: 4px;
        font-size: 18px;
      }
      .filter-label {
        padding: 4px;
      }
    }
  }
`;

export default TransactionsWrapper;
export const FilterResult = styled.div`
  color: ${({ theme }) => theme.colors.text};
  font-family: ${({ theme }) => theme.font};
  display: block;
  .result {
    display: flex;
    flex-direction: column;
    .list-item {
      display: flex;
      justify-content: space-between;
      overflow: hidden;
      margin-bottom: 5px;
      border: 1px solid #ccc;
      border-radius: 12px;
      padding: 10px;

      .info {
        display: flex;
        font-size: 18px;
        // background: red;
        .pro-pic {
          min-width: 60px;
          position: relative;
          max-width: 60px;
          max-height: 60px;
          img {
            border-radius: 16px;
            overflow: hidden;
            width: 100%;
          }
          span {
            position: absolute;
            background: #fff;
            padding: 3px;
            border-radius: 15px;
            top: -10px;
            left: -10px;
          }
          .Pending {
            color: #ffaf38;
          }
          .Complete {
            color: ${({ theme }) => theme.colors.green};
          }
          .Cancelled {
            color: ${({ theme }) => theme.colors.red};
          }
          .Overdue {
            color: ${({ theme }) => theme.colors.gray};
          }
        }
        .details {
          margin-left: 8px;
          width: 100%;
          .name {
            font-size: 14px;
            margin-bottom: 4px;
            color: ${({ theme }) => theme.colors.gray};
          }
          .amount {
            font-size: 18px;
            margin-bottom: 4px;
            font-weight: 600;
            color: ${({ theme }) => theme.colors.grayDark};
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 150px;
          }
          .date {
            font-size: 12px;
            margin-bottom: 3px;
          }
          .loc {
            font-size: 10px;
          }
        }
      }

      .action {
        display: flex;
        flex-direction: row;
        align-content: center;
        align-items: center;
        padding: 4px;
        // background: yellow;
        .icon {
          font-size: 18px;
          &.view {
            color: ${({ theme }) => theme.colors.primary};
          }
          &.edit {
            color: ${({ theme }) => theme.colors.secondary};
          }
          &.delete {
            color: ${({ theme }) => theme.colors.red};
          }
        }
      }
    }
  }
`;
export const ActionLink = styled(Link)`
  font-size: 24px;
  padding: 4px;
`;
