import React, { Component } from "react";
import { connect } from "react-redux";
import transactionsAction from "../../redux/transactions/actions";
import SearchBar from "./../../componants/Search/index";
import TransactionsWrapper, {
  FilterResult,
  ActionLink,
} from "./transactions.style";

import {
  ButtonBuySell,
  SectionTitle,
  Container,
} from "../../theme/GlobalStyles";
import { useSelector, useDispatch } from "react-redux";
import {
  HiOutlineQrcode,
  HiOutlineMinusCircle,
  HiOutlinePlusCircle,
  HiOutlineClock,
  HiOutlineCheckCircle,
  HiOutlineXCircle,
  HiOutlineEye,
  HiOutlinePencilAlt,
  HiOutlineTrash,
} from "react-icons/hi";
import XX from "../../constants/dummyList.js";
const {
  changeTransaction,
  addTransaction,
  editTransaction,
  deleteTransaction,
  viewChange,
} = transactionsAction;

const Transactions = ({ props }) => {
  //SEARCH
  const { search } = window.location;
  const query = new URLSearchParams(search).get("s");
  const [searchQuery, setSearchQuery] = React.useState(query || "");

  // const { list } = useSelector((state) => state.listReducer);
  const [status, setStatus] = React.useState("All");
  const [activeStatus, setActiveStatus] = React.useState({ activeLink: 0 });
  // const [myState, setMyState] = React.useState();
  const data = XX;
  const listTab = [
    {
      status: "All",
    },
    {
      status: "Pending",
    },
    {
      status: "Complete",
    },
    {
      status: "Cancelled",
    },
    {
      status: "Overdue",
    },
  ];
  const [dataList, setDataList] = React.useState(data);

  const setStatusFilter = (status) => {
    console.log(status);
    if (status !== "All") {
      setDataList([...data.filter((e) => e.status === status)]);
    } else {
      setDataList(data);
    }
    setStatus(status);
  };
  const renderStatus = (status, iconSize, iconColor) => {
    if (status == "All") {
      return null;
    }
    if (status == "Pending") {
      return <HiOutlineClock />;
    }
    if (status == "Complete") {
      return <HiOutlineCheckCircle />;
    }
    if (status == "Cancelled") {
      return <HiOutlineXCircle />;
    }
    if (status == "Overdue") {
      return <HiOutlineXCircle />;
    }
  };
  const filterPosts = (posts, query) => {
    if (!query) {
      return posts;
    }

    return posts.filter((post) => {
      const postName = post.name.first.toLowerCase();
      return postName.includes(query);
    });
  };

  const newDataList = filterPosts(dataList, searchQuery);

  return (
    <Container>
      <SearchBar searchQuery={searchQuery} setSearchQuery={setSearchQuery} />
      <SectionTitle>Transactions</SectionTitle>
      <TransactionsWrapper>
        <div className="filters-tab">
          {listTab.map((e, index) => (
            <div
              className="filter"
              className={
                index === activeStatus.activeLink ? "filter active" : "filter"
              }
              key={index}
              onClick={() => {
                setActiveStatus({ activeLink: index });
                setStatusFilter(e.status);
              }}
              // onClick={() => handleClick(link.id)}
            >
              {!e.status == "All" ? (
                <div className="filter-icon">
                  {renderStatus(e.status, index)}
                </div>
              ) : (
                ""
              )}
              <div className="filter-label">{e.status}</div>
            </div>
          ))}
        </div>

        {/* Filter Result */}
        <FilterResult>
          <div className="result">
            {newDataList.map((e, index) => (
              <div className="list-item" key={index} onClick={() => true}>
                <div className="info">
                  <div className="pro-pic">
                    {e.status == "Pending" ? (
                      <span className={e.status}>
                        <HiOutlineClock />
                      </span>
                    ) : (
                      ""
                    )}
                    {e.status == "Complete" ? (
                      <span className={e.status}>
                        <HiOutlineCheckCircle />
                      </span>
                    ) : (
                      ""
                    )}
                    {e.status == "Cancelled" ? (
                      <span className={e.status}>
                        <HiOutlineXCircle />
                      </span>
                    ) : (
                      ""
                    )}
                    {e.status == "Overdue" ? (
                      <span className={e.status}>
                        <HiOutlineXCircle />
                      </span>
                    ) : (
                      ""
                    )}
                    <img src={e.picture.medium} />
                  </div>
                  <div className="details">
                    <div className="name">
                      {e.name.first.charAt(0).toUpperCase() +
                        e.name.first.slice(1)}
                      {e.name.last}
                    </div>
                    <div className="amount">BTC {e.dob}</div>
                    <div className="date">{e.registered}</div>
                    <div className="loc">{e.nat}</div>
                  </div>
                </div>
                <div className="action">
                  <ActionLink
                    className="icon view"
                    to={{
                      pathname: "/dashboard/transaction-detail",
                      state: e,
                    }}
                  >
                    <HiOutlineEye />
                  </ActionLink>
                  <ActionLink className="icon edit">
                    <HiOutlinePencilAlt />
                  </ActionLink>
                  <ActionLink className="icon delete">
                    <HiOutlineTrash />
                  </ActionLink>
                </div>
              </div>
            ))}
          </div>
        </FilterResult>
      </TransactionsWrapper>
    </Container>
  );
};
export default Transactions;
